import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.css';
import './index.css';

//styles for video view items
import './video-view.css';
import './art-view.css';

import App from './App';
import registerServiceWorker from './registerServiceWorker';
import store from './MyStore.js';
import history from './MyStore'
import  {Provider } from 'react-redux';

import { BrowserRouter as Router, Route} from 'react-router-dom'


// When the route changes, dispatch that information to the store.
// Store.dispatch({type:"MENU_SELECT", section_to_show:"section-contact"});

ReactDOM.render(<Provider store={store}>
					<Router>
						<App />
					</Router>
				</Provider>, document.getElementById('root'));

registerServiceWorker();



