class AssetsPage extends Component
{
  constructor(props)
  {
    super(props);
  }

    onAssetTabSelected(index)
  {
    //only changes the tab selected if the index
    //of the new selsected one isn't the same as the current one
    if(index != this.props.assetTabSelected)
    {
      console.log("asset tab selected changed:", index);
      console.log("Asset Tab history:", this.props.history, this.props.location.pathname);

      //store.dispatch({type:"ASSET_SEL", assetTabSelected: index});

      var path = this.props.location.pathname;
      this.props.history.push(store.getState().home_path+"/assets/"+index);

      var match = matchPath(store.getState().home_path+"/assets/"+index, {
        path: store.getState().home_path+'/assets/:id',
        exact: false
      });

      if(match)
      {
        //store.dispatch({type:"ASSET_SEL",assetTabSelected: parseInt(match.params.id)});
      }

      console.log("MATCH,", match);
      console.log("##### >>>>>>>>>>> Match path:", match);
      //this.setState({assetTabSelected: index});
    }

  }

  render(){
    var selectedTab = 0;

    var state = store.getState();

    if(!state.is_logged_in && !state.development){
      return(<Redirect to={store.getState().home_path+"/login"} />);
    }


    //sets the selected tab to what ever the path match bring in
    if(this.props.match.params.hasOwnProperty('id'))
    {
      selectedTab = parseInt(this.props.match.params.id);
    }

    return(<div className="page-section-content" id="section-assets" data={store.getState().home_path+"/assets"}>

            <h3 className="page-section-content-title">My Assets</h3>
                <Tabs selectedIndex={selectedTab} onSelect={this.onAssetTabSelected.bind(this)} >
                  <TabList className="asset-tab-container">
                    <Tab className="asset-tab">MUSIC</Tab>
                    <Tab className="asset-tab">VIDEO</Tab>
                    <Tab className="asset-tab">ART</Tab>
                  </TabList>

                  <TabPanel>
                     <FileUploader />
                     <MusicView />
                  </TabPanel>

                  <TabPanel>
                    <VideoUploader />
                     <VideoView />
                  </TabPanel>
                  
                  <TabPanel>
                    <ArtUploader />
                     <ArtView />
                  </TabPanel>
                </Tabs>
          </div>)
  }
}

