import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { connect } from 'react-redux';

const mapStateToProps = (state) => {
  //console.log("LOGIN MENU mapStateToProps", state);
  return {show_wait_screen: state.show_wait_screen};
}

class LoadingScreen extends React.Component {
  constructor(props) {
    super(props);

/*
    this.state = {
      modal: false
    };*/

    //this.toggle = this.toggle.bind(this);
  }

  render() {
    return (
      <div>
        <Modal isOpen={this.props.show_wait_screen} className={this.props.className}>
          <ModalBody className="loading-screen-body"><div className="loading-spinner"></div></ModalBody>
        </Modal>
      </div>
    );
  }
}

export default connect(mapStateToProps)(LoadingScreen);


