import React, { Component } from 'react';
import { Button, ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import store from "./MyStore.js";
import { connect } from 'react-redux';
import {BrowserRouter as Router, Link, Route, withRouter} from 'react-router-dom';

const mapStateToProps = (state) => {
  //console.log("LOGIN MENU mapStateToProps", state);
  return {logged_in_user: state.logged_in_user,
  		is_logged_in: state.is_logged_in};
}

/*
const mapDispatchToProps = (dispatch) =>{
  //console.log("Dispatch LOGIN MENU maped", dispatch);
  return { login: (logged_in_user) => dispatch({type:"LOGIN_COMPLETE", logged_in_user: logged_in_user}) };
}*/


class LoginMenu extends Component
{
 constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      dropdownOpen: false
    };

    var state = store.getState();

    console.log("loginConstructor:", state, this.props);

    //this.setState({loginTitle: state.logged_in_user == "" ? "Login" : state.logged_in_user});
  }

  mainButtonClick(event)
  {

  	if(this.props.is_logged_in)
  	{
  		console.log("already logged in do nothing");
  		this.toggle();
  		return;
  	}

  	console.log("mainButtonPressed:", event);
    this.props.history.push(store.getState().home_path+"/login");
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  onSignOutClick()
  {
  	console.log("sign out pressed");
  	store.dispatch({type:"LOGOUT"});
    this.props.history.push(store.getState().home_path+"/");
  }

  onAssetsClick(event)
  {
    this.props.history.push(store.getState().home_path+"/assets/0");
  }

  createSignOutButton()
  {
  	var element = null;

  	if(this.props.is_logged_in)
  	{
  		element = <DropdownItem onClick={this.onSignOutClick.bind(this)}>Sign Out</DropdownItem>;
  	} else {
  		element = <DropdownItem disabled>Sign Out</DropdownItem>;
  	}

  	return element;
  }

  createAssetsButton()
  {
  	var element = <div/>;

  	if(this.props.is_logged_in)
  	{
  		element = <DropdownItem onClick={this.onAssetsClick.bind(this)}>Assets</DropdownItem>
  	} else{
  		element = <DropdownItem disabled>Assets</DropdownItem>
  	}

  	return element;
  }

  onProfileClick(event)
  {
    this.props.history.push(store.getState().home_path+"/profile");
  }

  createProfileButton()
  {
  	var element = <div/>

  	if(this.props.is_logged_in)
  	{
  		element = <DropdownItem onClick={this.onProfileClick.bind(this)}>Profile</DropdownItem>
  	} else{
  		element = <DropdownItem disabled>Profile</DropdownItem>
  	}
  	return element;
  }


  render() {
  	console.log("MENU LOGIN PROPS:", this.props);

  	//if logged in make title the email of user, else make the title Login
  	var mainButtonTitle = this.props.is_logged_in ? this.props.logged_in_user : "Login";
    return (
      <ButtonDropdown id="login-button-group" isOpen={this.state.dropdownOpen} toggle={this.toggle}>
    
          <Button id="caret" color="link" onClick={this.mainButtonClick.bind(this)}>{mainButtonTitle}</Button>
		  <DropdownToggle caret color="link" />
        <DropdownMenu right>
          {this.createAssetsButton()}
          {this.createProfileButton()}
          <DropdownItem>Become a Partner</DropdownItem>
          <DropdownItem divider />
          {this.createSignOutButton()}
        </DropdownMenu>
      </ButtonDropdown>
    );
  }
}

export default withRouter(connect(mapStateToProps)(LoginMenu));


