import React, { Component } from 'react';
import request from 'superagent';
import store from './MyStore.js';
import { connect } from 'react-redux';
import VideoUploader from './VideoUploader.js';

import {Button, ButtonDropdown, DropdownItem, DropdownToggle, DropdownMenu} from 'reactstrap';
import Pagination from 'react-js-pagination';

const mapStateToProps = (state) => {
  console.log("mapStateToProps", state);
  return {art_list: state.art_list,
          art_view_loading_overlay: state.art_view_loading_overlay};
}

const mapDispatchToProps = (dispatch) =>{
  //console.log("Dispatch maped", dispatch);
  return { refreshArtList: (art_list) => dispatch({type:"REFRESH_ART_LIST", art_list: art_list}) };
}


