class ArtView extends Component
{

  constructor(props)
  {
    super(props);
    this.state = {filter:"",
                  activePage: 1,
                  filteredItems: [],
                  itemsCountPerPage: 12,
                  error: false};
  }

  renderList()
  {
    var filter = this.state.filter;
    var items = this.props.art_list;

    console.log("renderList:", items, filter);
    if(items == undefined)
    {
      console.log("no ART ITEMS TO CREATE", this.props.art_list);
      return;
    }

    //console.log("render list called", items, filter);

    var filteredItems = items;

    if(filter != undefined || filter != "")
    {
      filteredItems = items.filter(function(item){

        var searchProperty = item.name;
        //searching without case sensitivity making both strings lowercase
        return (searchProperty.toLowerCase().includes(filter.toLowerCase()))
      });
    }


    var that = this;
    //this.setState({pageNumber: totalPages });
    var handleFilter = function(item, index)
    {
      var array = [];

      var maxPerPage = that.state.itemsCountPerPage;
      var activePage = that.state.activePage-1;
      var startIndex = (activePage) * maxPerPage;
      var endIndex = (activePage *maxPerPage) + maxPerPage;

      var maxIndex = that.props.art_list.length;
      console.log("startIndex:", startIndex, "maxIndex:", maxIndex, "endIndex:", endIndex);

      if(index < endIndex && index >= startIndex)
      {
        array.push(<ArtItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);
      }
      return array;
    }

    var listItems = filteredItems.map(handleFilter);

    return listItems;
  }

  onChangeFilter(event)
  {
    var searchQuery =  event.target.value;

    //event.preventDefault();
    this.setState({filter: searchQuery, activePage:1});
  }

  refreshArtList()
  {
    store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: true});

    var artistID = store.getState().artistID;//TODO: this needs to be updated on login right now usign a predefined default
    const GET_ALL_ART = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/asset-getAll?artistId="+artistID;

    let upload = request.get(GET_ALL_ART)
           .set('Accept', 'application/json');

    upload.on('progress', event => {

    console.log(event.percent);
      /* the event is:
      {
      direction: "upload" or "download"
      percent: 0 to 100 // may be missing if file size is unknown
      total: // total file size, may be missing
      loaded: // bytes downloaded or uploaded so far
      } */
    });

    upload.end((err, response) => {
      
      if (err) 
      {
        console.log("ERROR CAUGHT - response: ", response);
        throw console.error(err);
        return;
      }

      var responseBody = null;

      console.log("response: ",response, err);

      if(response)
      {
        if(response.hasOwnProperty("body"))
        {
          responseBody = response.body;

          //check to see if there was an error
          if(responseBody.hasOwnProperty("error"))
          {
            //if so alert it and abort anything else
            alert(response.body.error + "\n" + response.body.error_description);
            return;
          }
        }
      } 

      //check access denied
      if(response.status == 403)
      {
        throw console.error("request failed");
      }

      if(response.status == 200)
      {
        console.log("Request SUCCESSFUL");
        store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});

        var assets = response.body.assets;
        this.props.refreshArtList(assets);
        //store.dispatch({type: "REFRESH_video_TRACKS", tracks: tracks});
      } 

      if(response == undefined)
      {
        throw console.error("response was undefined", err, response);
        return;
      }
    });
  }

  createSearchBox()
  {
    return (<div className="art-search-box"><input placeholder="search" onChange={this.onChangeFilter.bind(this)}/> </div>);
  }

  createRefreshArt()
  {
    return(<div color="primary" className="btn-refresh" onClick={this.refreshArtList.bind(this)}><div className="icon-refresh"></div></div>)
  }

  handlePageChange(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    this.setState({activePage: pageNumber});
  }

  createPagination(totalCount)
  {
    return(
        <div className="pagination-container">
        <Pagination
          //hideDisabled
          //prevPageText='Prev'
          //nextPageText='Next'
          //firstPageText='First'
          //lastPageText='Last'

          activePage={this.state.activePage}
          itemsCountPerPage={this.state.itemsCountPerPage}
          totalItemsCount={totalCount}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        />
      </div>
      );
  }

  render(){
    var searchBox = this.createSearchBox();
    var artRefreshButton = this.createRefreshArt();

    var artOverlayClass = this.props.art_view_loading_overlay == false ? " art-hide" : "";

    var itemList = this.renderList(this.props.art_list, this.state.filter);
    var paginationControlls = this.createPagination(itemList.length);

    console.log(" >>>>>>> ART PROPS,", this.props);

    return(
      <ul className="art-item-view">
        {artRefreshButton}
        {searchBox}
        {paginationControlls}
        {itemList}

        <div className={"art-view-loading-overlay-container"+artOverlayClass}>
          <div className="pink-spiner"></div>
        </div>
      </ul>);
    }
}

class ArtItem extends Component
{

  constructor(props)
  {
    super(props);
     this.state = {url: "", 
                  error: false};

    this.toggle = this.toggle.bind(this);
  }

  compomentWillDismount()
  {
  }

  handleClick()
  {
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  onRemoveClicked(event)
  {
    console.log("removed pressed");
  }

  createItem(item)
  {
    var backgroundColors = ["#d2d2d2","#e2e2e2"];
    var mod = this.props.rowNumber  % 2;

    var style = {backgroundColor:backgroundColors[mod]};

    var errorClass = this.state.error ? " audio-error" : "";
    
    var iconClassName = "art-item-thumbnail";
    iconClassName += errorClass;

    var artName = item.name;

    return (<li className="art-item" style={style}>
              <p>{this.props.rowNumber+1}.</p>
              <div className="art-item-body">
                <img src={item.url} className={iconClassName} onClick={this.handleClick.bind(this)}></img>
                <p className="art-item-title">{artName}</p>
                <p className="art-item-description">{""}</p>
                  <ButtonDropdown color="primary" isOpen={this.state.dropdownOpen} toggle={this.toggle} >
                  <DropdownToggle className="music-item-dropdown">{<Dots />}</DropdownToggle>
                  <DropdownMenu right>
                    <DropdownItem color="warning" onClick={this.onRemoveClicked.bind(this)}>remove</DropdownItem>
                  </DropdownMenu>
                  </ButtonDropdown>
              </div>
            </li>);
  }

  render()
  {
    return(this.createItem(this.props.data));
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ArtView);


