import request from 'superagent';
import store from './MyStore.js';

export function getAllAudio(callback_)
{
	console.log("get all audio");
	    //curl -X GET "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-getAll" -H "accept: application/json"
    const GET_MUSIC_ALL = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-getAll";

    let upload = request.get(GET_MUSIC_ALL)
           .set('Accept', 'application/json');

    upload.on('progress', event => {
      console.log(event.percent);
        /* the event is:
        {
        direction: "upload" or "download"
        percent: 0 to 100 // may be missing if file size is unknown
        total: // total file size, may be missing
        loaded: // bytes downloaded or uploaded so far
        } */
    });

    upload.end((err, response) => {
      
      if (err) 
      {
        console.log("ERROR CAUGHT - response: ", response);
        throw console.error(err);
        return;
      }

      var responseBody = null;

      console.log("response: ",response, err);

      if(response)
      {
        if(response.hasOwnProperty("body"))
        {
          responseBody = response.body;

          //check to see if there was an error
          if(responseBody.hasOwnProperty("error"))
          {
            //if so alert it and abort anything else
            alert(response.body.error + "\n" + response.body.error_description);
            return;
          }
        }
      } 

      //check access denied
      if(response.status == 403)
      {
        throw console.error("request failed");
      }

      if(response.status == 200)
      {
        console.log("Request SUCCESSFUL");
        var tracks = response.body.tracks;

        if(callback_) callback_(tracks);
        //this.props.updateTracks(tracks);
        //store.dispatch({type: "REFRESH_MUSIC_TRACKS", tracks: tracks});
      } 

      if(response == undefined)
      {
        throw console.error("response was undefined", err, response);
        return;
      }
    });
}


export function getAllVideo(callback_)
{
    store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: true});

    console.log("get all video");

    const GET_ALL_VIDEO = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/video-getAll";

    let upload = request.get(GET_ALL_VIDEO)
           .set('Accept', 'application/json');

    upload.on('progress', event => {

    console.log(event.percent);
      /* the event is:
      {
      direction: "upload" or "download"
      percent: 0 to 100 // may be missing if file size is unknown
      total: // total file size, may be missing
      loaded: // bytes downloaded or uploaded so far
      } */
    });

    upload.end((err, response) => {

      var failed = false;

      if(response == undefined)
      {
        alert("something went wrong");
        failed = true;
        //throw console.error("response was undefined", err, response);
        return;
      }

      //check access denied
      if(response.status == 403)
      {
        alert("request failed");
        failed = true;
      }

      if(response.status == 200)
      {
        console.log("Request SUCCESSFUL");
        var videos = response.body.videos;

        console.log("video track response", response, videos);

        if(callback_) callback_(videos);
      }

      if(failed)
      {
        store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: false});
      }

    });
}

export function songDelete(audioID, callback_)
{

  //make audio view overlay
  store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: true});

  //var artistID = store.getState().artistID;//TODO: this needs to be updated on login right now usign a predefined default
  const DELETE_SONG_URL = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-delete";

  let upload = request.post(DELETE_SONG_URL)
         //.set('Accept', 'application/json')
         .send({'songId': audioID});

  upload.on('progress', event => {
    console.log(event.percent);
      /* the event is:
      {
      direction: "upload" or "download"
      percent: 0 to 100 // may be missing if file size is unknown
      total: // total file size, may be missing
      loaded: // bytes downloaded or uploaded so far
      } */
  });

  upload.end((err, response) => {

    console.log("response: ",response, err);

    if(response == undefined)
    {
      console.log("response", response);
      store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});
      return;
    }

    //check access denied
    if(response.status == 403)
    {
      store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});
      alert("something went wrong while deleting audio file")
      return;
    }

    //check access denied
    if(response.status == 404)
    {
      store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});
      alert(response.text);
      return;
    }

    if(response.status == 203)
    {
      store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});

      var assets = response.body.assets;
      if(callback_) callback_(assets);
    } 

  });// end onf end call upload
}

export function getAllArt(callback_)
{
  store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: true});

  var artistID = store.getState().artistID;//TODO: this needs to be updated on login right now usign a predefined default
  const GET_ALL_ART = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/asset-getAll?artistId="+artistID;

  let upload = request.get(GET_ALL_ART)
         .set('Accept', 'application/json');

  upload.on('progress', event => {

  console.log(event.percent);
    /* the event is:
    {
    direction: "upload" or "download"
    percent: 0 to 100 // may be missing if file size is unknown
    total: // total file size, may be missing
    loaded: // bytes downloaded or uploaded so far
    } */
  });

  upload.end((err, response) => {

    if(response == undefined)
    {
      alert("somthing went wrong");
      store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});
      return;
    }

    //check access denied
    if(response.status == 403)
    {
      alert("request failed");
      store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});
      return;
    }

    if(response.status == 200)
    {
      console.log("Request SUCCESSFUL");
      store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});

      var assets = response.body.assets;
      if(callback_) callback_(assets);
    } 

  });// end of upload.end((err, response) => {
}




