import React, { Component } from 'react';
import request from 'superagent';
import { createStore } from 'redux';

import { connect } from 'react-redux';

import store from "./MyStore.js";

import {getAllAudio, getAllVideo, getAllArt} from "./functions.js";
import {withRouter, Link} from 'react-router-dom'

const mapStateToProps = (state) => {
	//console.log("mapStateToProps", state);
	return {logged_in_user: state.logged_in_user};
}

const mapDispatchToProps = (dispatch) =>{
	//console.log("Dispatch maped", dispatch);
	return { registerUserThatLoggedIn: (username) => dispatch({type:"LOGIN_COMPLETE", logged_in_user: username}) };
}

var loginTokenInfo = {access_token: null,
						id_token: null,
						expires_in: null,
						scope: null,
						token_type: null};

class LoginForm extends Component
{
	constructor(props)
	{
		super(props)

		//this.state = loginStateChange(undefined, {});

		this.state = {username: "",
					password: ""};

		var du = "";
		var pw = "";

		if(store.getState().development)
		{
			du = "jsambuo+first@gmail.com";
			pw = "password";

			this.state.username = du;
			this.state.password = pw;
		}

		this.state.counter = 0;
		this.state.logged_in_user = "";
	}

	onUserChange(evt)
	{
		var text = evt.target.value;

		console.log("evt user", evt, text);

		this.setState({username: text});
	}

	onPasswordChange(evt)
	{
		var text = evt.target.value;
		console.log("evt password", evt);
		this.setState({password: text});
	}

	onKeyPress(event)
	{
		if(event.key === "Enter")
		{
			this.onLoginPressed();
		}
	}

	onAccessGranted(loginInfo)//{username: "mr.access"}
	{
		this.props.registerUserThatLoggedIn(this.state.username);

		//makes asset page show with option 0 music tab
		this.props.history.push(store.getState().home_path+"/assets/0");

		//dispatches to hide to loading screen
		store.dispatch({type:"WAIT_SCREEN", show_wait_screen: false});
		store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: true});
		store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: true});
		store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: true});

		getAllAudio(function(tracks){
			store.dispatch({type:"REFRESH_MUSIC_TRACKS", tracks:tracks});
			store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});
		});

		getAllVideo(function(tracks_video){
			store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: false});
			store.dispatch({type:"REFRESH_VIDEO_TRACKS", tracks_video: tracks_video});
		});

		getAllArt(function(art_list){
			store.dispatch({type:"OVERLAY_ART_VIEW", art_view_loading_overlay: false});
			store.dispatch({type:"REFRESH_ART_LIST", art_list: art_list})
		});
		//store.dispatch({type:"LOGIN_COMPLETE", logged_in_user: this.state.username});
	}

	onLoginPressed(evt)
	{

		if(this.state.password == "" || this.state.username == "")
		{
			alert("username or password cannot be blank");
			return;
		}

		store.dispatch({type:"WAIT_SCREEN", show_wait_screen: true});
		// console.log("login pressed");
		// console.log("user: ", this.state);
		// store.dispatch({type:"test", testProp: "modified"});

		//store.dispatch({type:"LOGIN_COMPLETE", logged_in_user: this.state.username});
		// console.log("logiPressTest:", this.props);
		// return;
		/*curl -X POST "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/user-login" -H "accept: application/json" -H "content-type: application/json" -d "{ \"username\": \"username\", \"password\": \"password\"}"*/
		//return;
		const LOGIN_API_URL = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/user-login";

		var loginCreds = {username:this.state.username,
						password:this.state.password};

		let upload = request.post(LOGIN_API_URL)
					 .set('Accept', 'application/json')
		            //.field('upload_preset', CLOUDINARY_UPLOAD_PRESET)
		            .send(loginCreds);
		            //.field('password', this.state.password)
		            //.field('d', this.state);

		// upload.progress();
		//console.log("uploadReq: ", upload);

		upload.on('progress', event => {

		console.log(event.percent);
			/* the event is:
			{
			direction: "upload" or "download"
			percent: 0 to 100 // may be missing if file size is unknown
			total: // total file size, may be missing
			loaded: // bytes downloaded or uploaded so far
			} */
		});

		upload.end((err, response) => {
			
			if(response)
			{
				if(response.status == 400)
				{
					alert("invalid username or password.");
					store.dispatch({type:"WAIT_SCREEN", show_wait_screen: false});
				}
			}

			// if (err) 
			// {
			// 	console.log("ERROR CAUGHT - response: ", response);
			// 	//throw console.error(err);
			// 	return;
			// }

			var responseBody = null;

			//console.log("response: ",response, err);

			if(response)
			{
				if(response.hasOwnProperty("body"))
				{
					//console.log("response", response.body);

					responseBody = response.body;

					//check to see if there was an error
					if(responseBody.hasOwnProperty("error"))
					{
						//if so alert it and abort anything else
						alert(response.body.error + "\n" + response.body.error_description);
						store.dispatch({type:"WAIT_SCREEN", show_wait_screen: false});
						return;
					}
				}
			}	

			//check access denied
			if(response.status == 403)
			{
				console.log(" ACCESS DENIED WRONG USER OR PASSWORD", response.body);
				alert("login failed, wrong username or password");
				store.dispatch({type:"WAIT_SCREEN", show_wait_screen: false});
				return;
			}

			if(response.status == 200)
			{
				console.log("LOGIN SUCCESSFUL");
				loginTokenInfo.access_token = response.body.access_token;
				loginTokenInfo.id_token = response.body.id_token;
				loginTokenInfo.token_type = response.body.token_type;
				loginTokenInfo.expires_in = response.body.expires_in;
				loginTokenInfo.scope = response.body.scope;

				//TODO: we need the artistID for uploads to work, on assets and music, and videos

				this.onAccessGranted(this.state);

				console.log("LoginTokenInfo: ", loginTokenInfo);
			} 

			if(response == undefined)
			{
				throw console.error("response was undefined", err, response);
				return;
			}
		});
	}

/*this.state = {username: "jsambuo+first@gmail.com",
		//			password: "password"};*/
	render()
	{
		var onlineString = "Offline";
		console.log("login render", this.props);

		if(this.props.logged_in_user != "")
		{
			onlineString = "Logged in as: ";
		}

		var du = "";
		var pw = "";

		return(<div id="login-form">
			<p>{onlineString + this.props.logged_in_user}</p>
			<div id="login-info-container">
				<p>Sign in to FutureFest VR</p>
				<p>If you don't already have an account, sign up <Link to={store.getState().home_path+"/register"}>here</Link></p>
			</div>
			
			<h3>EMAIL:</h3>
			<input className="login-input" placeholder="username" onChange={this.onUserChange.bind(this)} />
			<h3>PASSWORD:</h3>
			<input type="password" className="login-input" placeholder="password" onChange={this.onPasswordChange.bind(this)} onKeyPress={this.onKeyPress.bind(this)}/>
			<button className="login-button" onClick={this.onLoginPressed.bind(this)}>LOGIN</button>
			</div>);
	}
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(LoginForm));





