import React, { Component } from 'react';
import store from "./MyStore.js";
import { connect } from 'react-redux';
import LoginMenu from './LoginMenu';
import {BrowserRouter as Router, Link, Route, withRouter} from 'react-router-dom';

import history from "./MyStore.js"

//import  store.getState().home_path from './MyStore.js';

const connectStateToMainMenuProps = (state) => {
  console.log("MAIN MENU CONTROLLER mapStateToProps", state);
  return {section_to_show: state.section_to_show};
}


class MainMenu extends Component
{

  constructor(props)
  {
    super(props);
  }

  onClick(sectionId, event)
  {
    console.log("menu item clicked buttonNumber: ",sectionId);

    // if the index selected isn't the user menu index then set the state
    if(sectionId != this.props.location.pathname)
    {
      console.log(">>>>>>>>>> HISTORY",this.props);

      this.props.history.push(sectionId);
    }
  }

  getItems()
  {
    var items = [];

    var homePageMenu = {menuName:"HOME", elementId:store.getState().home_path+"/", hidden:false};
    var contactPageMenu = {menuName:"CONTACT", elementId:store.getState().home_path+"/contact", hidden:false};
    var profilePageMenu = {menuName:"PROFILE", elementId:store.getState().home_path+"/profile", hidden: true};

    var menuItemList = [homePageMenu, contactPageMenu, profilePageMenu];

    for(var i = 0; i < menuItemList.length; i++)
    {
      var isSelected = false;
      var addedClass = "";

      //var state = store.getState();
      //console.log("this store:",this, store);
      //console.log(">>>>> Menu Items:", menuItemList[i].elementId , this.props.section_to_show, this.props );
      //console.log("MAN MENU:", this.props.location.pathname, menuItemList[i].elementId);

      if(menuItemList[i].elementId == this.props.location.pathname) 
      {
        addedClass = "menu-selected";
      }

      var hidden = menuItemList[i].hidden;
      var sectionName = menuItemList[i].elementId;

      //if not hidden then show it !
      if(!hidden)
      {
        items.push(<div className={"menu-item " + addedClass} key={"menu-item-"+i} onClick={this.onClick.bind(this, sectionName)} > {menuItemList[i].menuName} </div>)
      }
    }

    return items;
  }

  createMenus(name){
    return(
        <div className="menu-container">{this.getItems()}
        {<LoginMenu />}

      </div>
        
      )
  }

  render(){
    return(this.createMenus());
  }
}


export default withRouter(connect(connectStateToMainMenuProps)(MainMenu));




