import { createStore } from 'redux'
/*
"id": "5125210512556032",
"thumbnailURL": "https://storage.googleapis.com/future-fest/videos/thumbnails/free-loops_Geometrix_Tunnel_5_H264.png",
"videoURL*/

/*  {
      "id": 5629499534213120,
      "key": "WyJBcnRpc3QiLCI1NjcyNzQ5MzE4MDEyOTI4IiwiQXNzZXQiLCI1NjM5Mjc0ODc5Nzc4ODE2Il0=",
      "name": "Chefboyarbeatz-White.png",
      "type": "overlay",
      "url": "https://storage.googleapis.com/future-fest/artists/5764878782431232/assets/Chefboyarbeatz-White.png"
    }*/

const appStore = (state = { username: "default",
                            counter: 0,
                            home_path:"/account",
                            development: false,// development flag, overrides login checks on other stuff
                            artistID: 5639445604728832,
                            logged_in_user: "",
                            is_logged_in:false,
                            tracks: [],
                            //tracks: [{title:"",id:"", artist:"name"}],
                            //tracks_video: [{id:"", videoURL:"video URL", thumbnailURL:"thumbnail URL"}],
                            tracks_video: [],
                            //art_list: [{id:123, key:"WyJBcnRpc3QiLCI1NjcyNzQ5MzE4MDEyOTI4IiwiQXNzZXQiLCI1NjM5Mjc0ODc5Nzc4ODE2Il0=", name:"Chefboyarbeatz-White.png", type:"overlay", url:"https://storage.googleapis.com/future-fest/artists/5764878782431232/assets/Chefboyarbeatz-White.png"}],
                            art_list: [],
                            section_to_show:"section-home-page" ,
                            show_wait_screen:false,
                            assetTabSelected: 0,
                            art_view_loading_overlay: false,
                            music_view_loading_overlay: false,
                            video_view_loading_overlay: false}, action) => {

	console.log("action: ", action);
  switch (action.type)
  {
    case "LOGIN_COMPLETE":
    	return Object.assign({}, state,{logged_in_user: action.logged_in_user, is_logged_in:true});
    break;

    case "MENU_SELECT":
	    console.log("state", state);
	    return Object.assign({},state,
	    		{actionType: action.type, section_to_show: action.section_to_show}
	    	);
	
    break;

    case "ASSET_SEL":
     console.log("asset sel", state);
      return Object.assign({},state,
          {actionType: action.type, assetTabSelected: action.assetTabSelected}
        );
    break;

    case "WAIT_SCREEN":
     console.log("state", state);
      return Object.assign({},state,
          {actionType: action.type, show_wait_screen: action.show_wait_screen}
        );
    break;

    case "REFRESH_MUSIC_TRACKS":
    	console.log("refresh music tracks:", action);
    	return Object.assign({},state,
		 	{actionType:action.type,tracks:action.tracks}
		);
    break;

    case "REFRESH_ART_LIST":
      console.log("refresh art list:", action);
      return Object.assign({},state,
      {actionType:action.type, art_list:action.art_list}
    );
    break;

    case "OVERLAY_ART_VIEW":
      console.log("overlay art view:", action);
      return Object.assign({},state,
      {actionType:action.type, art_view_loading_overlay:action.art_view_loading_overlay}
    );
      break;

    break;

    case "OVERLAY_MUSIC_VIEW":
    // music_view_loading_overlay
          console.log("overlay music view:", action);
      return Object.assign({},state,
      {actionType:action.type,music_view_loading_overlay:action.music_view_loading_overlay}
      );
    break;

    case "REFRESH_VIDEO_TRACKS":
      console.log("refresh video tracks:", action);
      return Object.assign({},state,
      {actionType:action.type,tracks_video:action.tracks_video}
    );
    break;

    case "OVERLAY_VIDEO_VIEW":
    // video_view_loading_overlay
          console.log("overlay video view:", action);
      return Object.assign({},state,
      {actionType:action.type,video_view_loading_overlay:action.video_view_loading_overlay}
      );
    break;

    case "ROUTE":
    console.log("ROUTE DETECTED!!!!!----", action.route,state);
     return Object.assign({},state,
          {actionType: action.type, route: action.route}
        );
    break;

    case "LOGOUT":
      return Object.assign({},state,
      {logged_in_user: "", is_logged_in:false, tracks: []})
    break;

    default:
	    return state;
    break;
  }
}


const store = createStore(appStore);

store.subscribe(() => {
	console.log("subscribe:  " ,store.getState());
});

export default store;


