import VideoUploader from './VideoUploader.js';

import {Button, ButtonDropdown, DropdownItem, DropdownToggle, DropdownMenu} from 'reactstrap';
import Pagination from 'react-js-pagination';

import {getAllVideo} from './functions.js';

const mapStateToProps = (state) => {
  console.log("mapStateToProps", state);
  return {tracks_video: state.tracks_video,
          video_view_loading_overlay: state.video_view_loading_overlay};
}

const mapDispatchToProps = (dispatch) =>{
  //console.log("Dispatch maped", dispatch);
  return { updateVideoTracks: (tracks_video) => dispatch({type:"REFRESH_VIDEO_TRACKS", tracks_video: tracks_video}) };
}


