class VideoView extends Component
{

  constructor(props)
  {
    super(props);
    this.state = {filter:"",
                  activePage: 1,
                  filteredItems: [],
                  itemsCountPerPage: 12,
                  error: false};
  }

  renderList()
  {
    var filter = this.state.filter;
    var items = this.props.tracks_video;

    console.log("renderList:", items, filter);
    if(items == undefined)
    {
      console.log("no video ITEMS TO CREATE", this.props.tracks_video);
      return;
    }

    //console.log("render list called", items, filter);

    var filteredItems = items;

    if(filter != undefined || filter != "")
    {
      filteredItems = items.filter(function(item){

        var searchProperty = item.videoURL;
        //searching without case sensitivity making both strings lowercase
        return (searchProperty.toLowerCase().includes(filter.toLowerCase()))
      });
    }


    var that = this;
    //this.setState({pageNumber: totalPages });
    var handleFilter = function(item, index)
    {
      var array = [];

      var maxPerPage = that.state.itemsCountPerPage;
      var activePage = that.state.activePage-1;
      var startIndex = (activePage) * maxPerPage;
      var endIndex = (activePage *maxPerPage) + maxPerPage;

      var maxIndex = that.props.tracks_video.length;
      console.log("startIndex:", startIndex, "maxIndex:", maxIndex, "endIndex:", endIndex);

      if(index < endIndex && index >= startIndex)
      {
        array.push(<VideoItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);
      }
      return array;
    }

    var listItems = filteredItems.map(handleFilter);

    //var listItems = filteredItems.map((item, index) => <VideoItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);

    return listItems;
  }

  onChangeFilter(event)
  {
    var searchQuery =  event.target.value;

    //event.preventDefault();
    this.setState({filter: searchQuery, activePage:1});
  }

  refreshvideo()
  {
    getAllVideo(function(videos){
      store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: false});
      store.dispatch({type:"REFRESH_VIDEO_TRACKS", tracks_video: videos});
    });
  }

  createSearchBox()
  {
    return (<div className="video-search-box"><input placeholder="search" onChange={this.onChangeFilter.bind(this)}/> </div>);
  }

  createvideoButton()
  {
    return(<div color="primary" className="btn-refresh" onClick={this.refreshvideo.bind(this)}><div className="icon-refresh"></div></div>)
  }

  handlePageChange(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    this.setState({activePage: pageNumber});
  }

  createPagination(totalCount)
  {
    return(
        <div className="pagination-container">
        <Pagination
          //hideDisabled
          //prevPageText='Prev'
          //nextPageText='Next'
          //firstPageText='First'
          //lastPageText='Last'

          activePage={this.state.activePage}
          itemsCountPerPage={this.state.itemsCountPerPage}
          totalItemsCount={totalCount}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        />
      </div>
      );
  }

  render(){
    var searchBox = this.createSearchBox();
    var videoButton = this.createvideoButton();

    var videoOverlayClass = this.props.video_view_loading_overlay == false ? " video-hide" : "";

    var itemList = this.renderList(this.props.tracks_video, this.state.filter);
    var paginationControlls = this.createPagination(itemList.length);

    console.log(" >>>>>>> VIDEO PROPS,", this.props);

    return(
      <ul className="video-item-view">
        {videoButton}
        {searchBox}
        {paginationControlls}
        {itemList}

        <div className={"video-view-loading-overlay-container"+videoOverlayClass}>
          <div className="pink-spiner"></div>
        </div>
      </ul>);
    }
}

